* {
   margin: 0;
   padding: 0;
   font-family: "montserrat", sans-serif;
}
.contact-section {
   padding: 100px 0;
   overflow: hidden;
   background: fixed;
   background: rgb(171, 198, 198);
   background: linear-gradient(
      0deg,
      rgba(171, 198, 198, 1) 52%,
      rgba(98, 125, 125, 1) 100%,
      rgba(180, 169, 144, 1) 100%
   );
}
.contact-section h1 {
   text-align: center;
   color: rgb(5, 2, 12);
}
.border {
   width: 100px;
   height: 10px;
   background: #34495e;
   margin: 40px auto;
}

.contact-form {
   max-width: 600px;
   margin: auto;
   padding: 0 10px;
   overflow: hidden;
}

.contact-form-text {
   display: block;
   width: 100%;
   box-sizing: border-box;
   margin: 16px 0;
   border: 0;
   background: #111;
   padding: 20px 40px;
   outline: none;
   color: #ddd;
   transition: 0.5s;
}
.contact-form-text:focus {
   box-shadow: 0 0 10px 4px #34495e;
}
textarea.contact-form-text {
   resize: none;
   height: 120px;
}
.contact-form-btn {
   float: right;
   border: 0;
   background: #34495e;
   color: #fff;
   padding: 12px 50px;
   border-radius: 20px;
   cursor: pointer;
   transition: 0.5s;
}
.contact-form-btn:hover {
   background: #2980b9;
}
